@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
}
ol, ul {
	list-style: none;
}
a{
  text-decoration: none;
}
body {
  font-family: 'Work Sans', sans-serif;
  font-weight: 400;
}
.About_about__vQBXJ {
  width: 100%;
  height: 100vh;
}
.About_about__vQBXJ h1 {
  text-align: center;
}
.Header_header__3C5BO {
  width: 100%;;
  height: calc(100vh - 60px);
  margin-top: 60px;
  background: linear-gradient(180deg, rgb(0, 0, 0, 0.7), rgba(0,0,0,0)), url(/restaurant-website/static/media/header-background.b4cc5e3a.jpg);
  background-size: cover;
  background-position: top;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
